import React, {Component} from "react";
import {Tabs, Tab, Grid, Cell, Card, CardTitle,CardText, CardActions, Button, CardMenu, IconButton} from "react-mdl";

class Project extends Component {
    constructor(props) {
        super(props);
        this.state = { activeTab: 0 };
    }
    toggleCategories() {
        switch (this.state.activeTab) {
            case 0:
                return (
                    <div className="projects-grid" id={this.props.id}>
                        <Card shadow={5} style={{minWidth: '450', margin:'auto'}}>
                            <CardTitle style={{color: '#fff', height: '176px', 
                                background: 'url(data:image/png;base64,) center / cover'}}> 
                                Wine And Dine
                            </CardTitle>
                            
                            <CardText >Wine And Dine</CardText>
                            <CardActions border>
                                <Button href="https://github.com/ObviouslyMartin/Wine-and-Dine-Base-App" raised colored>
                                    Github
                                </Button>
                                
                                
                            </CardActions>
                        </Card>
                        <Card shadow={5} style={{minWidth: '450', margin:'auto'}}>
                            <CardTitle style={{color: '#fff', height: '176px', 
                                background: 'url(data:image/png;base64,) center / cover'}}>
                                Blue Harvest
                            </CardTitle>
                            <CardText >Blue Harvest</CardText>
                            <CardActions border>
                                <Button href="https://github.com/ObviouslyMartin/BlueHarvestBP" alt="Blue Harvest" raised colored>
                                    Github
                                </Button>
                            </CardActions>
                        </Card>
                    </div>
                );
            case 1:
                return (
                    <div className="projects-grid" id={this.props.id}>
                        <Card shadow={5} style={{minWidth: '450', margin:'auto'}}>
                            <CardTitle style={{color: '#fff', height: '176px', 
                                background: 'url(data:image/png;base64,) center / cover'}}>
                                Eve Marketeer
                            </CardTitle>
                            <CardText >Eve Marketeer</CardText>
                            <CardActions border>
                                <Button href="https://github.com/biginferno/EveMarketeer" raised colored>
                                    Github
                                </Button>
                            </CardActions>
                        </Card>
                    </div>
                );
            default:
                return (
                    <div><h1>We broke at switch(default)</h1></div>
                )
        }
    }
    render() {
        return(

            <div className="category-tabs">
                <Tabs activeTab={this.state.activeTab} onChange={(tabId) => this.setState({activeTab: tabId})} ripple>
                    <Tab>School</Tab>
                    <Tab>Personal</Tab>
                </Tabs>
                    <Grid >
                        <Cell col={12}>
                            <div className="content"> {this.toggleCategories()}</div>
                        </Cell>
                    </Grid>

            </div>
        )
    }
}
export default Project;